footer {
  overflow: hidden;

  .footer-contact {
    padding: 30px 70px;
    background: $light-blue-for-block;

    .footer-subscribe {

      h4 {
        position: relative;
        padding-bottom: 10px;

        &:before {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 25%;
          max-width: 80px;
          height: 2px;
          background: $blue-content;
        }
      }

      form {
        margin-top: 15px;
      }
    }
  }

  .footer-info {
    padding-top: 20px;
    padding-bottom: 20px;
    @include boxShadow(0 -2px 4px 0px rgba(0, 0, 0, 0.2));

    img {
      height: 12px;
      width: auto;
      display: inline-block;
      margin-right: 5px;
    }
  }
}

@include breakpoint(mmd) {

  footer {

    .footer-contact {
      padding: 5vw 15px;
    }

    .footer-info {
      padding-top: 4vw;
      padding-bottom: 4vw;

      img {
        height: 3.5vw;
      }
    }
  }
}


