.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: rgba(0, 0, 0, 0.25);
  top: 0;
  left: 0;

  &#popup-subscriber, &#loading {
    visibility: hidden;
    display: none;
    opacity: 0;
  }

  .popup-inner {
    @include objAbsoluteMiddle;
    width: 90%;
    max-width: 992px;
    @include boxShadow(0px 0px 5px 0px rgba(0, 0, 0, 0.75));
    background: #fff;
    padding: 40px;
  }

  &#loading {
    background: url("images/loading.gif") center no-repeat;
    background-size: 32px 32px;
    z-index: 99999;
  }
}




