header {
  background: rgba(0, 48, 105, 0.75);
  padding: 10px 15px;

  a {
    border-bottom: 2px solid rgba(0,0,0,0);
    position: relative;
    display: inline-block;

    &:hover {

      &:before {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background: #fff200;
      }
    }
  }
}

.page-visual-slider {
  position: relative;

  .single-visual {
    height: 650px;

    &[order="1"] {
      background: url("images/visual-1.jpg") top center no-repeat;
      background-size: cover;
    }

    &[order="2"] {
      background: #ee2abf;
      background-size: cover;
    }

    &[order="3"] {
      background: #0f0;
      background-size: cover;
    }
  }

  .visual-text-container {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    .visual-text {
      position: absolute;
      left: 15px;
      top: 50%;
      @include customTransform(translateY(-50%));

      .content {
        display: inline-block;
        position: relative;
        padding-bottom: 15px;
        margin-bottom: 20px;

        &:before {
          position: absolute;
          content: '';
          width: 100%;
          height: 2px;
          bottom: 0;
          left: 0;
          background: #fff200;
        }
      }
    }
  }
}

@include breakpoint(mmd) {

  header {

    &.navbar {
      padding: 3vw;

      img {
        height: 6vw;
        width: auto;
      }

      a {

        &:hover {

          &:before {
            max-width: 20px;
          }
        }
      }
    }
  }

  .page-visual-slider {

    .visual-text-container {

      .visual-text {
        left: 0;
        right: 0;
        margin: 0 auto;

        .content {

          &:before {
            width: 30vw;
            right: 0;
            margin: 0 auto;
          }
        }
      }
    }
  }
}


